$primary    : #007bff;
$secondary  : #6c757d;
$success    : #28a745;
$danger     : #dc3545;
$warning    : #ffc107;
$info       : #17a2b8;
$light      : #f8f9fa;
$dark       : #343a40;

$theme-colors: () !default;
$theme-colors: map-merge(
    (
        "primary"   : $primary,
        "secondary" : $secondary,
        "success"   : $success,
        "info"      : $info,
        "warning"   : $warning,
        "danger"    : $danger,
        "light"     : $light,
        "dark"      :  $dark
    ),
    $theme-colors
);