#sec-head {
    div {
        position: fixed;
        top: 58px;
        right: 10px;
        z-index: 9000;
        max-width: 300px;
    }
}

#sec-nav {
    .navbar {
        height  : $nav-height;
        padding : 0px;

        &.bg-dark {
            background-color: $color-dark !important;
        }

        
    }

    .navbar-brand {
        margin-left: 10px;
    }

    .nav-icon {
        $border: 9px;
        $size: $nav-height - ($border*2);

        border          : 0px;
        border-radius   : 0px;
        padding         : $border;

        height  : $nav-height;
        width   : $nav-height;

        img{
            height  : $size;
            width   : $size;
        }
    }
}

#sec-cont {
    $height: $nav-height + 15;
    margin-top: $height;
    margin-bottom: $height;
}

#sec-foot {
    footer.footer{
        background-color: $color-dark;
        #copyright{
            background-color: #151515;
        }
    }
}