@import url('https://fonts.googleapis.com/css2?family=Lexend+Deca&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Source+Code+Pro&display=swap');

/* global variables */
$nav-height: 48px;

$color-primary: #3278be;
// $color-secondary: ;
$color-success: #6dc7a2;
$color-danger: #e04062;
$color-warning: #eba601;
// $color-info
// $color-light
$color-dark: #202020;
// $color-muted
// $color-white

@import 'bootstrap';
@import 'components';
@import 'section';

/* master */

* {
    font-family: 'Lexend Deca', sans-serif;
}

body {
    background-color: #F3F2F1;
}

/* components */

.repo {
    &-icon {
        box-shadow: 0px 1px 15px rgba(30,30,30,.15);
        max-width: 150px;
    }
}

/* sidebar */

.highlight{
    margin-top: 5px;
    padding-left: 5px;
    border-left: 5px solid #404040;
}

.hover {
    transition: 0.3s;
    &:hover {
        @extend .cursor-pointer;
        background-color: rgba(255,255,255,0.1);
    }
}

#sidebar {
    $size: 300px;

    background: $color-dark;
    box-shadow: 0px;
    color: #ffffff;
    width   : $size;


    height  : 100vh;
    position: fixed;
    top     : 0;
    left    : -$size;
    z-index : 9999;
    transition: all 0.3s;
    overflow-y: scroll;

    a {
        color: white;
    }

    &.active {
        left: 0;
        box-shadow: 3px 3px 3px rgba(0,0,0,0.2);
    }

    #dismiss {
        height: $nav-height;

        #right.nav-icon{
            $border: 4px;
            $size: $nav-height - ($border*2);

            height: $size;
            width: $size;
            margin: $border;
        }
    }
}

.overlay {
    display     : block;
    visibility: hidden;
    position    : fixed;
    width       : 100vw;
    height      : 100vh;
    background  : rgba(0,0,0,0.7);
    z-index     : 9998;
    opacity     : 0;
    transition  : all 0.25s;

    top:0;
    left:0;
    right:0;
    bottom:0;

    &.active {
        visibility:visible;
        opacity : 1;
    }
}
