/* cursor */
.cursor {
    &-default {
        cursor: default;
    }
    &-pointer {
        cursor: pointer;
    }
}

/* anchor */
section.anchor {
    display: block;
    position: relative;
    top: -50px;
    visibility: hidden;
}

/* header link */
.hlink-header {
    &:hover a, a:hover {
        opacity: 1;
    }

    a {
        float: left;
        opacity: 0;
        margin-left: -25px;
        margin-right: 4px;
        font-size: 25px;

        i {
            color: #6c757d;
        }
    }
}

h1.hlink-header a {
    margin-top: 16px;
}

h2.hlink-header a {
    margin-top: 10px;
}

h3.hlink-header a {
    margin-top: 8px;
}

h4.hlink-header a {
    margin-top: 6px;
}

p,b,i,h5 {
    &.hlink-header a {
        margin-top: 3px;
    }
}