@import 'variables';

a.a-hover:hover.unset{
    color: #007bff;
    text-decoration: none;
}

.breadcrumb {
    background-color: unset;
}

.carousel {
    height: 360px;
}

code span, .text-monospace {
    font-family: 'Source Code Pro', 'Consolas', 'Courier New', Courier, monospace !important;
}

.container {
    max-width: 1200px;
}

.nav-pills .nav-link.nav-link-sm {
    padding: .25rem .5rem;
    border-radius: .2rem;
    font-size: .875rem;
}

.row {
    margin: 0px;
    .row & {
        width   : 100%;
    }
}